.aboutPageContainer {
    width: 100vw;
    display: flex;
    justify-content: center;
}

.aboutPage {
    width: 90vw;
}

.instrumentImgContainer img {
    height: 100px;
    padding-bottom: 15px;
}

.instrumentName {
    padding: 20px 0 15px 0;
}

.bandDescription p {
    width: 100%;
    color: #e0e0e0;
    line-height: 22px;
    margin: 15px 0 0 0;
    font-size: 24px;
}

.aboutPage .aboutBand {
    margin: 30px 0;
    color: #222327;
    background: transparent !important;
}

.aboutPage .aboutBand .title {
    font-size: 20px;
    padding: 15px;
    color: #e0e0e0;
}

.aboutPage .aboutBand .description {
    font-size: 18px;
    padding: 15px;
    text-align: justify;
    color: #e0e0e0;
}

.instrumentsContainer {
    padding: 10px 15px;
}

.instrumentGrid {
    margin: 0 -12px !important;
}

.aboutPage .instrumentName, .aboutPage .musician {
    color: #e0e0e0 !important;
    font-size: 1rem;
}

.aboutPage .card {
    margin: 30px 0;
}

.aboutPage .card:hover {
    -webkit-filter: drop-shadow(0px 3px 0.8px #000);
            filter: drop-shadow(0px 3px 0.8px #000);
    cursor: pointer;
}

.aboutPage .bandPhotoContainer {
    background: url(http://bellflowerband.com/desktopPortraitGroup.jpg);
    background-size: cover;
    height: 420px;
    width: 100vw;
    position: relative;
    background-position: center;
    opacity: 0.5;
    right: 5.6%;
}

.imgAndInfoContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80vw;
    margin: 0 auto;
    background: linear-gradient(90deg, #b32539, #801335);
    -webkit-filter: drop-shadow(1px 1px 2px black);
            filter: drop-shadow(1px 1px 2px black);
}

.bandMemberImgContainer {
    margin: 30px 15px 30px 30px;
    width: 60%;
}

.bandMemberImgContainer img {
    object-fit: cover;
    position: relative;
    width: 100%;
    -webkit-filter: drop-shadow(1px 1px 2px black);
            filter: drop-shadow(1px 1px 2px black);
    opacity: .67;
    height: 308px;
}

.bandMemberInfoContainer {
    width: 40%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
    margin: 30px 30px 30px 15px;
    height: 100%;
    color: #e0e0e0;
    font-family: sans-serif;
}

@media only screen and (max-width: 899px) {
    .imgAndInfoContainer {
        flex-direction: column;
        width: 85vw;
        min-height: 450px;
    }

    .bandMemberInfoContainer {
        width: 90%;
        margin: 20px;
        justify-content: flex-start;
        height: 60%;
        overflow-y: scroll;
    }

    .bandMemberInfoContainer p {
        font-size: 18px;
    }

    .bandMemberInfoContainer h4 {
        margin: 10px 0;
    }

    .bandMemberImgContainer {
        -webkit-filter: drop-shadow(1px 1px 2px black);
                filter: drop-shadow(1px 1px 2px black);
        width: 100%;
        height: 40%;
        margin: 0;
        overflow: hidden;
    }

    .bandMemberImgContainer img {
        height: unset;
        max-height: 400px;
        width: calc(100% - 40px);
        margin: 40px 20px 0 20px;
        object-fit: contain;
    }
}

@media only screen and (max-width: 899px) and (orientation: landscape) {
    .imgAndInfoContainer {
        flex-direction: row;
        min-height: unset;
    }
    .bandMemberImgContainer img {
        margin: 0px 20px 0 20px;
    }
}

.calendarPageContainer {
    width: 90vw;
    margin: 0 auto;
}
.calendarPageContainer .numOfEvents {
    margin-top: 88px;
    color: #222327;
    background: transparent !important;
}
.calendarPageContainer .title {
    font-size: 20px;
    padding: 15px;
    color: #e0e0e0;
}
.calendarPageContainer .eventListContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.eventCard {
    border: 2px solid #e0e0e0;
    padding: 15px;
    color: #e0e0e0;
    margin: 30px;
    width: 70vw
}

.detailsContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.detailsContainer .details {}

.detailsContainer .locationImage {
    height: 300px;
}
.detailsContainer .locationImage img {
    height: 100%;
    width: 100%;
    object-fit: cover;
    opacity: .7;
}
.eventCard .material-icons {
    margin-right: 15px;
}

.eventCard .hasIcon {
    display: flex;
    align-items: flex-start;
    text-align: left;
    font-size: 18px;
}

.directions, .openInCalendar {
    text-decoration: none;
    color: inherit;
}

